import React from 'react'
import './Products.css'

// import '../LoginRegister/sass/loginStyle.scss'
import { Formik, Form, Field } from 'formik';
import * as Yup from 'yup';
import ProductApi from './data/ProductApi'
import {Select,MenuItem} from '@material-ui/core/';
import {withRouter} from 'react-router-dom'


const SignupSchema = Yup.object().shape({
    name: Yup.string()
      .min(2, 'Too Short!')
      .max(50, 'Too Long!')
      .required('Required'),
    price: Yup.string()
      .min(1,' Enter minimum eight Characters')
      .required('Required'),
    quantity: Yup.string()
      .min(1,' Enter minimum eight Characters')
      .required('Required'),
    description: Yup.string()
      .min(2,' Enter minimum eight Characters')
      .required('Required'),
    
});

 class UpdateForm extends React.Component{
    constructor(props){
        super(props);
        this.state={
            id:this.props.product.id,
            name:this.props.product.name,
            description:this.props.product.description,
            quantity:this.props.product.quantity,
            price:this.props.product.price,
            type:this.props.product.type,
            isLoggedIn:localStorage.getItem('isloggedin'),
            date:this.props.product.date,
            time:this.props.product.time,
            updatedDate:null,
            updatedTime:null,
            views:this.props.product.views,
            status:""
        }
    }
    componentDidMount(){
        console.log("IN UPDATE FORM")
        console.log(this.state)
    }
    getAuthenticate = async() => {
        this.setState({isLoggedIn:localStorage.getItem('isloggedin')})
        console.log("Before setImout")
        await setTimeout(()=> { console.log("Inside SetTimeout")},5000)
    }
    submit = async(values) => {
        this.setState({name:values.name,description:values.description,quantity:values.quantity,price:values.price});
        var date = new Date();
        this.setState({
            updatedDate: date.getDate() +"/"+ (date.toLocaleString('default', { month: 'short' })) +"/"+ date.getFullYear(),
            updatedTime: date.getHours()+":"+ date.getMinutes()+":"+ date.getSeconds()
        })
        
        let body ={
            "name":this.state.name,
            "description":this.state.description,
            "price":this.state.price,
            "quantity":this.state.quantity,
            "type":this.state.type,
            "updatedDate":this.state.updatedDate,
            "updatedTime":this.state.updatedTime,
            "date":this.state.date,
            "time":this.state.time,
            "views":this.state.views
        }
        // console.log("Before autheticate")
        await this.getAuthenticate();
        // console.log("BefoAfterre autheticate")
        // console.log(localStorage.getItem('isloggedin'));
        // !localStorage.getItem('isloggedin') ? this.props.history.push('/login') :
        if(JSON.parse(localStorage.getItem('isloggedin'))){
            console.log("BODY DATA");
            console.log(body)
            ProductApi.updateProduct(this.state.id,body)
                .then(res => {
                    this.setState({status:"Product Udated Successfully"});
                    console.log(res);
                })
        }
        else
            this.props.history.push('/login');
    }

    
    render(){
        return(
            <div className="box-containers ">
                {/* <div className="root-containers"> this and below code looks like popup window */}
                    <div className='root-container popup'>  
                        <div className='popup\_inner'>
                            <span className="status" >{this.state.status}</span>
                        {/* <div className="root-container "> this and above code looks like popup window */}
                            <div className="box-container inner-container">
                                <div className="header">
                                Add Products
                                </div>
                                <div className="box">
                                <Formik
                                    initialValues={{
                                        name:this.state.name,
                                        price:this.state.price,
                                        quantity:this.state.quantity,
                                        description:this.state.description,
                                        type:this.state.type
                                    }}
                                    validationSchema={SignupSchema}
                                    onSubmit={values => {
                                        this.submit(values)
                                    }}
                                    >
                                    {({ errors, touched }) => (
                                        <Form>
                                        <div className="input-group">
                                            <label htmlFor="name">Product Name</label>
                                            <Field name="name" placeholder='Product Name' className="login-input" />
                                            <small className="danger-error">{errors.name && touched.name ? (
                                            <div>{errors.name}</div>
                                            ) : null}
                                            </small>
                                        </div>
                                        <div className="input-group">
                                            <label htmlFor="quantity">Quantity</label>
                                            <Field name="quantity" placeholder='Quantity' className="login-input" /> 
                                            <small className="danger-error">{errors.quantity && touched.quantity ? (
                                            <div>{errors.quantity}</div>
                                            ) : null}
                                            </small>
                                        </div>
                                        <div className="input-group">
                                            <label htmlFor="price">Price</label>
                                            <Field name="price" placeholder='Price' className="login-input" /> 
                                            <small className="danger-error">{errors.price && touched.price ? (
                                            <div>{errors.price}</div>
                                            ) : null}
                                            </small>
                                        </div>
                                        <div className="input-group">
                                            <label htmlFor="description">Description</label>
                                            <Field name="description" placeholder='Description' className="login-input" /> 
                                            <small className="danger-error">{errors.description && touched.description ? (
                                            <div>{errors.description}</div>
                                            ) : null}
                                            </small>
                                        </div>
                                        {/* <div className="input-group">
                                            <label htmlFor="type">Type</label>
                                            <Field name="type" placeholder='Type' className="login-input" /> 
                                            <small className="danger-error">{errors.type && touched.type ? (
                                            <div>{errors.type}</div>
                                            ) : null}
                                            </small>
                                        </div> */}
                                        <div className="input-group">
                                            <label htmlFor="type">Type</label>
                                            <Select
                                                name="type"
                                                type='drop-down'
                                                value={this.state.type}
                                                onChange={(e)=> this.setState({type:e.target.value})}

                                                // onBlur={handleBlur}
                                                // style={{ display: 'block' }}
                                            >
                                                {/* {this.state.updateProduct.type} */}
                                                <MenuItem value={""}>Select a category</MenuItem>
                                                <MenuItem value={"Food"}>Food</MenuItem>
                                                <MenuItem value={"Electronics"}>Electronics</MenuItem>
                                                <MenuItem value={"Stationery"}>Stationery</MenuItem>
                                                {/* <option value="" label="Select a category" />
                                                <option value="Electronics" label="Electronics" />
                                                <option value="Food" label="Food" />
                                                <option value="Stationery" label="Stationery" /> */}
                                            </Select>
                                            <small className="danger-error">
                                                {errors.type && touched.type &&
                                                <div className="input-feedback">
                                                {errors.type}
                                                </div>}
                                            </small>
                                        </div>
                                        <button type='submit' className="login-btn">Update Product</button>
                                        </Form>
                                    )}
                                    </Formik>
                                    <br></br>
                                    <button className="btn btn-secondary" onClick={() => this.props.closePopup(null)}>close window</button>
                                </div>
                            </div>
                            </div>

                        </div>  
                    </div>
            //     </div>
            // </div>
        )
    }
}


export default withRouter(UpdateForm)