import React from "react";
import "./sass/loginStyle.scss";
// import TransitionGroup from "react-transition-group"
import FadeTransition from "./transition/fadeTransition";
import LoginBox from "./LoginBox";
import RegisterBox from "./RegisterBox";

export default class Loginapp extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoginOpen: true,
      isRegisterOpen: false,
    };
  }

  showLoginBox() {
    this.setState({ isLoginOpen: true, isRegisterOpen: false });
  }

  showRegisterBox() {
    this.setState({ isRegisterOpen: true, isLoginOpen: false });
  }
  //------------------my addition code
  userLoggedIn = (username, isLoggedIn) => {
    console.log("Username in Loginapp" + username);
    this.props.userLogged(username, isLoggedIn);
  };
  //-----------------------
  render() {
    return (
      <div className="root-container">
        <div className="box-controller">
          <div
            className={ 
              "controller " + (this.state.isLoginOpen ? "selected-controller" : "" )
            }
            onClick={this.showLoginBox.bind(this)}
          >
            Login
          </div>
          <div
            className={
              "controller " + (this.state.isRegisterOpen ? "selected-controller" : "")
            }
            onClick={this.showRegisterBox.bind(this)}
          >
            Register
          </div>
        </div>

        <FadeTransition isOpen={this.state.isLoginOpen} duration={500}>
          <div className="box-container">
            <LoginBox props={this.props} userLogged={this.userLoggedIn} />
          </div>
        </FadeTransition>
        <FadeTransition isOpen={this.state.isRegisterOpen} duration={500}>
          <div className="box-container">
            <RegisterBox props={this.props} />
          </div>
        </FadeTransition>
      </div>
    );
  }
}
