// import { createBrowserHistory } from 'history';
import React from "react";
import './sass/loginStyle.scss'
import { Formik, Form, Field } from 'formik';
import * as Yup from 'yup';
import UserApi from "./data/UserApi";
import {withRouter} from 'react-router-dom'

const SignupSchema = Yup.object().shape({
  email: Yup.string()
    .min(4, 'Too Short!')
    .max(50, 'Too Long!')
    .email('Invalid email')
    .required('Required'),
  password: Yup.string()
    .min(8,' Enter minimum eight Characters')
    .required('Required'),
});



class LoginBox extends React.Component {

    constructor(props) {
      super(props);
      this.state = {
        password:"",
        status:{statusState:false,statusMessage:""}
      };
    }
  
    // submitLogin(e) {}
  //   login(loginUserData){
  //     console.log("Cming");
  //     axios.get('http://localhost:3001/user')
  //         .then(user => this.checkLoginData(user.data,loginUserData))
  //   }
  // checkLoginData(usersData,loginUserData) {
  //     let flag=0
  //     for(let user of usersData){
  //         flag=0;
  //         if((user.username === loginUserData.username) & (user.password === loginUserData.password)){
  //             return console.log("User Loggedin ");
  //         }
  //         else{
  //             flag=1;
  //         }
  //     }
  //     if(flag===1){
  //         return console.log("Username and password may wrong. \n Enter Correct Username and pasword")
  //     }
  // }
  
    render() {
      return (
        <div className="inner-container">
          <div className="header">
            Login
          </div>
          <div className="box">
            <Formik
                initialValues={{
                    password: '',
                    email: '',
                }}
                validationSchema={SignupSchema}
                onSubmit={values => {
                  console.log("Coming");
                    this.setState({email:values.email,password:values.password});
                    console.log(this.state.email);
                    console.log(this.state.password);
                    UserApi.login(this.state)
                        .then(res => {
                          this.setState({status:res});
                          console.log(res);
                          if(res.statusMessage==="User Logged In"){
                              this.props.userLogged(this.state.email,true);
                              this.props.history.push("/product")
                          }
                        }
                    )
                  }
                }
                >
                {({ errors, touched }) => (
                  <Form>
                    <div className="input-group">
                      <label htmlFor="email">Email</label>
                      <Field name="email" placeholder='Email' className="login-input" /> 
                      <small className="danger-error">{errors.email && touched.email ? (
                        <div>{errors.email}</div>
                        ) : null}
                      </small>
                    </div>
                    
                    <div className="input-group">
                      <label htmlFor="password">Password</label>
                      <Field type='password' name="password" placeholder='Password' className="login-input"/>
                        <small className='danger-error'>{errors.password && touched.password ? (
                          <div>{errors.password}</div>
                          ) : null}
                        </small>
                    </div>
                    <p className='status'>{this.state.status.statusMessage}</p>
                    <p className='status'>{this.state.status.statusState}</p>
                    <button type='submit' className="login-btn">Login</button>

                  </Form>
                )}
              </Formik>
          </div>
        </div>
      );
    }
  
  }
  export default withRouter(LoginBox)
// function pop(props) {
//   return
// }





