import React from "react";
// import './sass/loginStyle.scss'
// import { useFormik } from 'formik';
import { Formik, Form, Field } from 'formik';
import * as Yup from 'yup';
import UserApi from "./data/UserApi";
// import axios from 'axios';
const phoneRegExp = /^((\\+[1-9]{1,4}[ \\-]*)|(\\([0-9]{2,3}\\)[ \\-]*)|([0-9]{2,4})[ \\-]*)*?[0-9]{3,4}?[ \\-]*[0-9]{3,4}?$/
const SignupSchema = Yup.object().shape({
    firstname: Yup.string().min(4, 'Too Short!')
      .max(50, 'Too Long!')
      .required('Required'),
    lastname: Yup.string()
      .min(4, 'Too Short!')
      .max(50, 'Too Long!')
      .required('Required'),
    email: Yup.string()
      .min(2, 'Too Short!')
      .max(50, 'Too Long!')
      .email('Invalid email')
      .required('Required'),
    password: Yup.string()
      .min(8,' Enter minimum eight Characters')
      .required('Required'),
    phone: Yup.string().matches(phoneRegExp, 'Phone number is not valid'),
    location:Yup.string()
      .min(3, 'Too Short!'),
});

export default class RegisterBox extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      firstname: "",
      lastname:"",
      phone:"",
      location:"",
      email: "",
      password: "",
      errors: [],
      pwdState: null,
      status:""
    };
  }
  // checkUserDetails(userRegData){
  //   axios.get("http://localhost:3001/user")
  //       .then(res => {this.checkUser(res.data,userRegData)})
  //       .catch(err => {console.log(err);console.log("dnsdbsdbdshjdsgjhsdfgjhdsgfhgsjdgfjhgsdjfgjsdgf=====")});
  // }

  // checkUser(res,userRegData){
  //   let flag=0;
  //   for(let user of res){
  //     flag=0;
  //     if(userRegData.username === user.username || userRegData.email === user.email){
  //         return(this.setState({status:"Username or email id Already exists"}));
  //     }
  //     else{
  //         flag=1;
  //     }
  //   }
  //   if(flag==1)
  //       this.addUserRegData(userRegData);
  // }
  // addUserRegData(userRegData){
  //   const body = { 
  //       username:userRegData.username,
  //       email:userRegData.email,
  //       password:userRegData.password
  //   }
  //   axios.post('http://localhost:3001/user', body)
  //       .then(respose => this.setState({status : "Username added"}))
  // }
  render() {

    return (
      <div className="inner-container">
        <div className="header">
          Register
        </div>
        <div className="box">
          

        <Formik
            initialValues={{
              firstname: "",
              lastname:"",
              phone:"",
              location:"",
              email: "",
              password: "",
              date:"",
              time:""
            }}
            validationSchema={SignupSchema}
            onSubmit={(values,resetForm) => {
                // same shape as initial values
                this.setState({
                    firstname:values.firstname,
                    lastname:values.lastname,
                    phone:values.phone,
                    email:values.email,
                    password:values.password,
                    location:values.location,
                });
                var date = new Date();
                this.setState({
                    date: date.getDate() +"/"+ (date.toLocaleString('default', { month: 'short' })) +"/"+ date.getFullYear(),
                    time: date.getHours()+":"+ date.getMinutes()+":"+ date.getSeconds()
                })

                UserApi.checkUserDetails(this.state)
                    .then(res => this.setState({status:res}));
                resetForm();
            }}
            >
            {({ errors, touched }) => (
              <Form>
                <div className="input-group">
                  <label htmlFor="firstname">Firstname</label>
                  <Field name="firstname" placeholder='Firstname' className="login-input" /> 
                  <small className="danger-error">{errors.firstname && touched.firstname ? (
                    <div>{errors.firstname}</div>
                    ) : null}
                  </small>
                </div>
                <div className="input-group">
                  <label htmlFor="lastname">Lastname</label>
                  <Field name="lastname" placeholder='lastname' className="login-input" /> 
                  <small className="danger-error">{errors.lastname && touched.lastname ? (
                    <div>{errors.lastname}</div>
                    ) : null}
                  </small>
                </div>
                
                <div className="input-group">
                  <label htmlFor="email">Email</label>
                  <Field name="email" placeholder='Email' className="login-input"/>
                  <small className="danger-error">{errors.email && touched.email ? (
                    <div>{errors.email}</div>
                    ) : null}
                  </small>
                </div>
                <div className="input-group">
                  <label htmlFor="mobile">Mobile Number</label>
                  <Field type='mobile' name="phone" placeholder='Mobile Number' className="login-input"/>
                    <small className='danger-error'>{errors.phone && touched.phone ? (
                      <div>{errors.phone}</div>
                      ) : null}
                    </small>
                </div>
                <div className="input-group">
                  <label htmlFor="password">Password</label>
                  <Field type='password' name="password" placeholder='Password' className="login-input"/>
                    <small className='danger-error'>{errors.password && touched.password ? (
                      <div>{errors.password}</div>
                      ) : null}
                    </small>
                </div>
                <div className="input-group">
                  <label htmlFor="location">Location</label>
                  <Field type='location' name="location" placeholder='Location' className="login-input"/>
                    <small className='danger-error'>{errors.location && touched.location ? (
                      <div>{errors.location}</div>
                      ) : null}
                    </small>
                </div>
                <p className='status'>{this.state.status}</p>
                <button type='submit' className="login-btn">Register</button>
                
              </Form>
              
            )}
          </Formik>
{/*           
          <div className="input-group">
            <label htmlFor="username">Username</label>
            <input
              type="text"
              name="username"
              className="login-input"
              placeholder="Username"
              onChange={this
              .onUsernameChange
              .bind(this)}/>
            <small className="danger-error">{usernameErr
                ? usernameErr
                : ""}</small>
          </div>
          

          <div className="input-group">
            <label htmlFor="email">Email</label>
            <input
              type="text"
              name="email"
              className="login-input"
              placeholder="Email"
              onChange={this
              .onEmailChange
              .bind(this)}/>
            <small className="danger-error">{emailErr
                ? emailErr
                : ""}</small>
          </div>
          
          <div className="input-group">
            <label htmlFor="password">Password</label>
            <input
              type="password"
              name="password"
              className="login-input"
              placeholder="Password"
              onChange={this
              .onPasswordChange
              .bind(this)}/>
            <small className="danger-error">{passwordErr
                ? passwordErr
                : ""}</small>

            {this.state.password && <div className="password-state">
              <div
                className={"pwd pwd-weak " + (pwdWeak
                ? "show"
                : "")}></div>
              <div
                className={"pwd pwd-medium " + (pwdMedium
                ? "show"
                : "")}></div>
              <div
                className={"pwd pwd-strong " + (pwdStrong
                ? "show"
                : "")}></div>
            </div>}

          </div>

          <button
            type="button"
            className="login-btn"
            onHover={this
            .openPopup
            .bind(this)}
            onClick={this
            .openPopup
            .bind(this)}>Register</button>
        </form>*/}
        </div>
      </div> 

    );

  }

}








