import React, { Component } from 'react';
import ProductApi from './data/ProductApi'
import Checkbox from '@material-ui/core/Checkbox';
import './Products.css'
import DirectionsIcon from '@material-ui/icons/Directions';
import MenuIcon from '@material-ui/icons/Menu';
import SearchIcon from '@material-ui/icons/Search';
// import {SearchIcon, DirectionsIcon, MenuIcon} from '@material-ui/icons'
import {TextField, Paper, IconButton, InputBase,  Divider } from '@material-ui/core'
import {Col, Form,InputGroup, FormControl} from 'react-bootstrap'
import {Link,NavLink} from 'react-router-dom'
import UpdateForm from './UpdateForm'
import {withRouter} from 'react-router-dom'
import TopViewedProducts from './TopViewedProducts'
import { isEmptyArray } from 'formik';




class ProductList extends Component {
    constructor(props){
        super(props);
        this.state={
            clicked:false,
            productList:[],
            search:"",
            id:true,
            name:true,
            price:true,
            des:true,
            updatedDate:true,
            addedDate:true,
            qty:true,
            type:true,
            updateProduct:[],
            updateComponent:false,
            isLoggedIn:this.props.statedata.isLoggedIn,
            proddetails:[],
            status:""
        }
    }
    componentDidMount(){
        this.getProducts();
    }
    getProducts = () => {
        ProductApi.getProducts()
        .then(res =>  this.setState({productList:res}))
        .catch(err => console.log(err))

    }
    search = (e) => {
        if(this.state.search==="")
            ProductApi.getProducts().then(res => this.setState({productList:res}))
        else
            ProductApi.searchProduct(this.state.search).
            then(res =>{
                if(!(isEmptyArray(res)))
                    this.setState({productList:res, status:""})
                else{
                    this.setState({status:"No Resuluts Found under the search criteria"})
                    console.log("No results found") 
                }
            })
    }
    delete = async(product) =>{
        await this.reroute();
        if(this.props.statedata.isLoggedIn)
            ProductApi.deleteProduct(product).then(res =>  this.setState({productList:res.data}));
        else
            this.props.history.push('login')
    }
    update = async (product) => {
        await this.reroute();
        setTimeout(() => {
            if(this.props.statedata.isLoggedIn)
            {
                console.log("Inside if "+this.state.isLoggedIn)
                this.setState({updateProduct:product,updateComponent:true})
                setTimeout(() => {
                    // this.getProducts();
                },100)
                console.log("UPDTAE PRODUCT DATA")
                console.log(this.state.updateProduct)
            }
            else
                this.props.history.push('/login')
        }, 1000);
    }
    // viewDetails = async(value) => {
    //     await this.reroute();
    //     if(this.props.statedata.isLoggedIn){
    //         // (<><ViewProductDetails productData={value}/></>)
    //         // this.props.proddata(value);
            // <NavLink to={`/product/${value.name}`}>{value}</NavLink>
    //         // this.setState({proddetails:value})
    //     }
    // }
    reroute = () => {
        this.setState({isLoggedin:this.props.statedata.isLoggedIn,updateComponent:false})
    }
    render() {
        let cardSize={
            width: "20rem",
        }
        return (
            <div className='container'>
            <div className="">
                <div className='pad'>
                    <div className="col-sm-3 left"> 
                        <NavLink activeClassName="active"  to={'/addproduct'} ><button className="btn btn-success">Add Products Page </button></NavLink><br/>
                        {/* <input type="text" placeholder ="Search Product Name" className=""
                            onChange={
                                e => {
                                    this.setState({search:e.target.value});
                                    setTimeout(()=>{ this.search(e)},500)}}
                        /><i className="fas fa-search " style={{backgroundColor:"white",padding:"7px"}}></i> */}
                        <div className="row" style={{padding:"15px"}}>
                            <Paper > 
                                <InputBase style={{padding:"7px"}}
                                    placeholder="Search Product Names"
                                    inputProps={{ 'aria-label': 'search google maps' }}
                                    onChange={
                                        e => {
                                            this.setState({search:e.target.value});
                                            setTimeout(()=>{ this.search(e)},500)
                                        }}
                                />
                                <IconButton type="submit" aria-label="search">
                                    <SearchIcon />
                                </IconButton>
                                <Divider  orientation="vertical" />
                                
                            </Paper>
                        </div>
                            
                    </div >
                    <TopViewedProducts />
                </div>
                {/* <label className="label label-heading col-sm-6">Customize your table values</label> */}
                <div className='label'>
                    <div>
                        <label className="label-heading">Customize your table values</label>
                    </div>
                    <label>
                        <Checkbox checked={this.state.id} onChange={ () => {this.setState({id:!this.state.id})}}
                            inputProps={{ 'aria-label': 'primary checkbox' }}/>
                        ID
                    </label>
                    <label>
                        <Checkbox checked={this.state.type} onChange={ () => {this.setState({type:!this.state.type})}}
                            inputProps={{ 'aria-label': 'primary checkbox' }}/>
                        TYPE
                    </label>
                    <label>
                        <Checkbox disabled checked inputProps={{ 'aria-label': 'disabled checked checkbox' }} />
                        Name
                    </label>
                    <label>
                        <Checkbox checked={this.state.price} onChange={() => {this.setState({price:!this.state.price})}}
                            inputProps={{ 'aria-label': 'primary checkbox' }}/>
                        Price
                    </label>
                    
                    <label>
                        <Checkbox checked={this.state.updatedDate} onChange={() => {this.setState({updatedDate:!this.state.updatedDate})}}
                            inputProps={{ 'aria-label': 'primary checkbox' }}/>
                        Recent updated Date
                    </label>
                    <label>
                        <Checkbox checked={this.state.addedDate} onChange={() => {this.setState({addedDate:!this.state.addedDate})}} 
                            inputProps={{ 'aria-label': 'primary checkbox' }}/>
                        Added Date 
                    </label>
                    <label>
                        <Checkbox checked={this.state.des} onChange={ () => {this.setState({des:!this.state.des})}} 
                            inputProps={{ 'aria-label': 'primary checkbox' }}/>
                        Description
                    </label>
                    <label>
                        <Checkbox checked={this.state.qty} onChange={() => {this.setState({qty:!this.state.qty})}}
                            inputProps={{ 'aria-label': 'primary checkbox' }}/>
                        Available Quantity
                    </label>
                </div>
                </div>
                <div className="row">
                    {
                        (this.state.status==='') ? 
                            (
                                this.state.productList.map((value,index) => {
                            // (value.name ==='') ? return (<div> No record Found</div>) : 
                                    return(
                                        <div key={index} className="col-sm-4">
                                            <div className="card" style={cardSize} >
                                                {/* <img className="card-img-top" src="..." alt="Card image cap"> */}
                                                <div className="card-body">
                                                    <h5 className="card-title">{value.name}</h5>
                                                    < >{this.state.id && (<><label >ID : {value.id}</label><br></br></>)}</>
                                                    <>{this.state.name && (<><label>Name : {value.name}</label> <br></br></>)}</>
                                                    <>{this.state.des && (<><label>Description : {value.description}</label> <br></br></>)}</>
                                                    < >{ this.state.price && (<><label>Price : {value.price}</label> <br></br></>)}</>
                                                    < >{this.state.qty && (<><label>Quantity : {value.quantity}</label> <br></br></>)}</>
                                                    < >{this.state.type && (<><label>Type : {value.type}</label> <br></br></>)}</>
                                                    < >{this.state.addedDate && (<><label>Date : {value.date}</label> <br></br></>)}</>
                                                    < >{this.state.addedDate && (<><label>Time : {value.time}</label> <br></br></>)}</>
                                                    < >{this.state.addedDate && (<><label>Updated Date : {value.updatedDate}</label> <br></br></>)}</>
                                                    < >{this.state.addedDate && (<><label>Updated Time : {value.updatedTime}</label> <br></br></>)}</>
                                                    < >{this.state.addedDate && (<><label>Views : {value.views}</label> <br></br></>)}</>
                                                    {/* <p className="card-text">Some quick example text to build on the card title and make up the bulk of the card's content.</> */}
                                                    {/* <a href="#" className="btn btn-primary">Go somewhere</a> */}
                                                </div>
                                                <button className="btn btn-secondary" ><Link style={{"textDecoration":"none","color":"white"}} to={`/product/${value.name}`}><i className="fas fa-eye"></i> View {value.name} Details</Link></button>
                                                <button className="btn btn-primary" onClick={() => {this.update(value)}}><i className="fas fa-marker"></i> UPDATE</button>
                                                <button className="btn btn-danger" onClick={()=> this.delete(value)}><i className="fa fa-trash fa-lg"></i> DELETE</button>
                                            </div>
                                        </div>
                                    )
                                })
                            ) : <div  className="col-12" style={{color:"red", backgroundColor:"white", textAlign:"center", fontSize:"25px"}}>{this.state.status}</div>
                    }
                    {
                        this.state.updateComponent ? <UpdateForm product={this.state.updateProduct} closePopup={() => this.setState({updateComponent:false})}/> : null 
                        // (this.state.isLoggedIn && this.state.updateComponent) ? <UpdateForm product={this.state.updateProduct} closePopup={this.update}/> : null
                    }
                </div>
            </div>
        );
    }
}

export default withRouter(ProductList);