import "./Style.css";
import Loginapp from "../LoginRegister/Loginapp";
import {  NavLink, BrowserRouter as Router, Route, Switch} from "react-router-dom";
import React from "react";
import Home from "../Home/Home";
import Contacts from "../Contacts/Contacts";
import About from "../Contacts/About";
import ProductList from "../Products/ProductList";
import AddProduct from "../Products/AddProduct";
import Profile from "../LoginRegister/Profile";
import ViewProductDetails from "../Products/ViewProductdetails";

class NavBar extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoggedIn: localStorage.getItem("isloggedin"),
      darkmode: true,
      loggedInUser: localStorage.getItem("username"),
      proddetails: null,
    };
  }
  userLoggedIn = (username, isLoggedIn) => {
    this.setState({ isLoggedIn: isLoggedIn, loggedInUser: username });
    this.props.Appprops.userloggedin(username, isLoggedIn);
  };
  userLoggedOut = () => {
    this.props.Appprops.userloggedout(false, null);
    console.log("calling 2" + localStorage.getItem("isloggedin"));
    this.setState({ isLoggedIn: false, loggedInUser: null });
  };
  componentDidMount() {
    this.setState({
      isLoggedIn: this.props.Appprops.isloggedin,
      loggedInUser: this.props.Appprops.loggedinuser,
    });
  }
  myStyle = { color: "Green","fontWeight":"bold" };
  // proddata = (value) => {
  //     this.setState({proddetails:value})
  // }
  render() {
    const userLoggedIn = (
      <ul className={"nav navbar-nav ml-auto"}>
        <li>
          <NavLink exact activeClassName="active" className="link" to={"/"}>
            <i className="fas fa-home"></i> Home
          </NavLink>
        </li>
        <li style={this.myStyle}> {this.state.loggedInUser}</li>
        <li>
          <span onClick={(e) => this.userLoggedOut()} > <i className="fas fa-sign-out-alt"></i> Logout </span>
        </li>
        <li>
          <NavLink exact activeClassName="active" className="link" to={"/profile"}>
            <i className="fa fa-user fa-fw"/> Profile
          </NavLink>
        </li>
        <li>
          <NavLink exact activeClassName="active" className="link" to={"/contacts"}>
            <i className="far fa-id-card"></i> Contact
          </NavLink>
        </li>
        <li>
          <NavLink exact activeClassName="active" className="link" to={"/about"}>
            About
          </NavLink>
        </li>
      </ul>
    );
    const guestLoggedIn = (
      <ul className={"nav navbar-nav ml-auto"}>
        <li>
          <NavLink exact activeClassName="active" className="link" to={"/"}>
            <i className="fas fa-home"></i> Home
          </NavLink>
        </li>
        <li>
          <NavLink exact activeClassName="active" className="link" to={"/login"}>
            <i className="fas fa-sign-in-alt"></i> Login
          </NavLink>
        </li>
        <li>
          <NavLink exact activeClassName="active" className="link" to={"/contacts"}>
            <i className="far fa-id-card"></i> Contact
          </NavLink>
        </li>
        <li>
          <NavLink exact activeClassName="active" className="link" to={"/about"}>
            About
          </NavLink>
        </li>
      </ul>
    );

    return (
      <div>
        <Router className="container">
          <div className="dark-mode">
            <nav
              className={
                "navbar navbar-expand-lg navbar-light bg-dark fixed-top"
              }
            >
              <a className="navbar-brand" href="/">
                {/* <span style={{color:"pink"}}>MCart</span> */}
                <img className="brand" src={require("../../Images/Brand/favicon.ico")} alt="icon not available  "></img>
              </a>
              <button
                className="navbar-toggler"
                type="button"
                data-toggle="collapse"
                data-target="#navbarNav"
                aria-controls="navbarNav"
                aria-expanded="false"
                aria-label="Toggle navigation"
              >
                <span className="navbar-toggler-icon"></span>
              </button>
              <div className="collapse navbar-collapse" id="navbarNav">
                {/* <ul className="nav navbar-nav ml-auto"> */}
                {/* <li><NavLink exact activeClassName="active"  to={'/'} > Home </NavLink></li>
                  <li><NavLink exact activeClassName="active"  to={'/login'} >Login</NavLink></li>
                  <li><NavLink exact activeClassName="active"  to={'/contacts'} >Contact</NavLink></li>
                  <li><NavLink exact activeClassName="active"  to={'/about'} >About</NavLink></li> */}
                {/* <li><NavLink exact activeClassName="active"  to={this.state.pages.pageLink} >{this.state.pages.displayName}</NavLink></li> */}
                {this.state.isLoggedIn || !this.state.loggedInUser === null
                  ? userLoggedIn
                  : guestLoggedIn}

                {/* </ul> */}
              </div>
            </nav>
          </div>
          <Switch>
            <Route exact path="/" component={Home} />
            <Route
              exact
              path="/Login"
              render={(props) => <Loginapp userLogged={this.userLoggedIn} />}
            />
            <Route
              exact
              path="/product"
              render={(props) => <ProductList statedata={this.state} />}
            />
            <Route exact path="/product/:name" component={ViewProductDetails} />
            <Route
              exact
              path="/profile"
              render={(props) => (
                <Profile loggedInUser={this.state.loggedInUser} />
              )}
            />
            <Route exact path="/addproduct" component={AddProduct} />
            <Route exact path="/contacts" component={Contacts} />
            <Route exact path="/about" component={About} />
          </Switch>
        </Router>
      </div>
    );
  }
}

export default NavBar;

