import React, { useState, useEffect } from 'react';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import { Line } from 'react-chartjs-2'
// import {Doughnut, defaults} from 'react-chartjs-2';
import ProductApi from './data/ProductApi';

export const TopViewedProducts = () =>  {
    const [topViewed, setTopViewed] = useState(3);
    const [productsList, setProductsList] = useState([]);
    const [Productdata, setProductData]= useState([])
    let TOP_VIEWED_PRODUCT_NAME = new Array();
    let TOP_VIEWS = new Array;
    let sampledata = {};
    const [data, setData] = useState(sampledata)
    useEffect(() => {
        ProductApi.topViewedproducts(topViewed).then(res => {setProductsList(res.data); 
            setTimeout(() => {
                let i=0;
                for( ;i<topViewed;i++)
                {
                    TOP_VIEWED_PRODUCT_NAME.push(res.data[i].name);
                    TOP_VIEWS.push(res.data[i].views);
                }
                setProductData(TOP_VIEWED_PRODUCT_NAME)
                sampledata = {
                    labels: TOP_VIEWED_PRODUCT_NAME,
                    datasets:[
                        {
                            label: "VIEWS",
                            backgroundColor: "purple",
                            data: TOP_VIEWS,
                            borderColor:"white",
                            borderWidth: 2,
                            // height:1000,
                            // width:1000,
                            dataColor:"black",
                            dataFontColor:"black",
                            // label: 'My First dataset',
                            // fill: false,
                            lineTension: 0.5,
                            borderColor: 'rgba(75,192,192,1)',
                            // borderCapStyle: 'butt',
                            // borderDash: [],
                            // borderDashOffset: 0.0,
                            // borderJoinStyle: '',
                            pointBorderColor: 'white',
                            pointBackgroundColor: 'green',
                            // pointBorderWidth: 1,
                            // pointHoverRadius: 5,
                            pointHoverBackgroundColor: 'rgba(75,192,192,1)',
                            pointHoverBorderColor: '#ffffff',
                            pointHoverBorderWidth: 2,
                            pointRadius: 3,
                            pointHitRadius: 10,
                        }
                    ]
                }
                setData(sampledata)
            }, 1000);
        });
        
    },[topViewed])

    return (
        <div className="right col-sm-4">
            <FormControl style={{width:"200px", textAlign:"center"}}>
                <InputLabel  style={{fontSize:25}} id="demo-customized-select-label"><i className="fas fa-eye fa-1x"></i> Top Views</InputLabel><br/>
                <Select
                    labelId="demo-customized-select-label"
                    id="demo-customized-select"
                    value={topViewed}
                    onChange={(e) => setTopViewed(e.target.value)}
                >
                    <MenuItem value="">
                        <em>None</em>
                    </MenuItem>
                    <MenuItem value={3}>3</MenuItem>
                    <MenuItem value={5}>5</MenuItem>
                    <MenuItem value={10}>10</MenuItem>
                </Select>
            </FormControl> 
            <div>
                {/* <Chart  axes={axes} tooltip /> */}
                <div style={{position:"obsolute"}} width="100%" height="100%">
                    <Line options={{responsive:false, maintainAspectRatio: true, 
                        title:{
                            display:true,
                            text:`TOP ${topViewed} Viewed Products`,
                            fontSize:20,
                            fontColor:"black",
                            axisColor:"black"
                        },
                        legend:{
                            display:true,
                            // position:'right'
                        } }} data={data} width={300}
                        height={300}/>
                </div>
            </div>
            {/* <div>
                {
                    productsList.map((value,index) => {
                        return(
                            <ol key={index}>
                                <li>{value.name}</li>{value.views}
                            </ol>
                        )}
                    )
                }
            </div> */} 
        </div>
    );
}

export default TopViewedProducts;
