import React from 'react'
import "../LoginRegister/sass/loginStyle.scss"
import {NavLink}  from 'react-router-dom'
import {Carousel} from 'react-bootstrap'
// import { Carousel } from 'react-responsive-carousel';
// import Images from '../Images/carousel/Food.jpeg'

export default function Home() {
    const [index, setIndex] = React.useState(0);
    const handleSelect = (selectedIndex, e) => {
        setIndex(selectedIndex);
    };
    return (
        <div className='root-container'>
            <Carousel style={{width:"35%"}} activeIndex={index} onSelect={handleSelect}>
                <Carousel.Item className="">
                    <img 
                        className="d-block w-100" 
                        // className="" style={{margin:"auto"}}
                        src={require('../../Images/carousel/Food.jpeg')}//"/800x400?text=First slide&bg=373940"
                        alt="First slide"
                    />
                </Carousel.Item>
                <Carousel.Item>
                    <img
                        className="d-block w-100" 
                        // className=" w-100" style={{width:"10%",height:"10%"}}
                        src={require('../../Images/carousel/Stationary.jpeg')}//"holder.js/800x400?text=Second slide&bg=282c34"
                        alt="Second slide"
                    />
                </Carousel.Item>
                <Carousel.Item>
                    <img 
                        className="d-block w-100" 
                        // className=" w-100" style={{width:"10%",height:"10%"}}
                        src={require('../../Images/carousel/Electronics.jpeg')}
                        alt="Third slide"
                    />
                </Carousel.Item>
            </Carousel>
            <br/>
            <span style={{fontSize:"25px",color:"white",textAlign:"center"}}>Click <span style={{color:"blue"}}>Products</span> button to view All Products</span>
            <NavLink exact activeClassName="active"  to={'/Product'} > <button className="btn btn-primary">Products</button></NavLink>
        </div>
    )
}
