import React, { Component } from 'react';
import ProductApi from './data/ProductApi'
import {withRouter} from 'react-router-dom'
import './Products.css'

 class ViewProductDetails extends Component {
  constructor(props){
    super(props);
    this.state={
      id:"",
      name:"",
      type:"",
      price:"",
      description:"",
      quantity:"",
      date:"",
      updatedDate:"",
      time:"",
      updatedTime:"",
      img:null
    }
  }
  componentDidMount(){    
    if(!JSON.parse(localStorage.getItem('isloggedin')))
       return this.props.history.push('/login')
    else{
        return this.setProducts();
    }
  }
  setProducts = async() => {
      await ProductApi.searchProduct(this.props.match.params.name).then( res => { this.setState({
          id : res[0].id,
          name : res[0].name,
          type : res[0].type,
          price : res[0].price,
          description : res[0].description,
          quantity : res[0].quantity,
          date : res[0].date,
          updatedDate : res[0].updatedDate,
          time : res[0].time,
          updatedTime : res[0].updatedTime,
          views: res[0].views
      })
      const body ={
        "name": this.state.name,
        "description": this.state.description,
        "price": this.state.price,
        "quantity":this.state.quantity,
        "type": this.state.type,
        "date": this.state.date,
        "time": this.state.time,
        "id": this.state.id,
        "updatedDate":this.state.updatedDate,
        "updatedTime" : this.state.updatedTime,
        "views":(this.state.views+1)
      }
      console.log(`product name = ${this.state.name}`)
      this.setState({img : require(`../../Images/products/${this.state.name}.jpeg`)});
      ProductApi.updateProduct(this.state.id,body).then(res => console.log(res))    
    })
  }
  
  render() {
    return (
      <div className="containers">
          {/* <div className="box-left-containers"> */}
              <img className='col-sm-6' style={{float:"left"}} src={this.state.img} width="45%" height="10%"  alt="Imgae not uploaded"></img>
              <div className='col-sm-5 prod'style={{float:"right",margin:"auto"}}>
                  {/* <label className="prod col-sm-4">Name </label> <span className="prod col-sm-2">:</span> <span className="prod col-sm-1">{this.state.name}</span><br/>
                  <label className="prod col-sm-4">Price </label> <span className="prod col-sm-2">:</span> <span className="prod col-sm-1">{this.state.price}</span><br/>
                  <label className="prod col-sm-4">Available Quantity </label> <span className="prod col-sm-2">:</span> <span className="prod col-sm-1">{this.state.quantity}</span><br/>
                  <label className="prod col-sm-4">Added Timestamp </label> <span className="prod col-sm-2">:</span> <span className="prod col-sm-1">{this.state.date} {this.state.time}</span><br/>
                  <label className="prod col-sm-4">Last Updated Timestamp </label> <span className="prod col-sm-2">:</span> <span className="prod col-sm-1">{this.state.updatedDate}{this.state.updatedTime}</span><br/>
                  <label className="prod col-sm-4">Views </label> <span className="prod col-sm-2">:</span> <span className="prod col-sm-1">{this.state.Views}</span><br/> */}
                  <label className="prod">Name : <span>{this.state.name}</span></label><br/>
                  <label className="prod">Price : <span>{this.state.price}</span></label><br/>
                  <label className="prod">Available Quantity : <span>{this.state.quantity}</span></label><br/>
                  <label className="prod">Added Date : <span>{this.state.date}</span></label><br/>
                  <label className="prod">Added Time : <span>{this.state.time}</span></label><br/>
                  <label className="prod">Updated Date : <span>{this.state.updatedDate}</span></label><br/>
                  <label className="prod">Updated Time : <span>{this.state.updatedTime}</span></label><br/>
                  <label className="prod">Views : <span>{this.state.views}</span></label><br/>
{/*                   
                  <span>{this.state.price}</span><br/>
                  <span>{this.state.description}</span><br/>
                  <span>{this.state.quantity}</span><br/>
                  <span>{this.state.date}</span><br/>
                  <span>{this.state.time}</span><br/>
                  <span>{this.state.updatedDate}</span><br/>
                  <span>{this.state.updatedTime}</span><br/>
                  <span>{this.state.views}</span><br/> */}
              </div>
          {/* </div> */}
      </div>
    );
  }
}
export default withRouter(ViewProductDetails)